<template>
  <form @submit.prevent="updateFac" >
            <div>
                      
                 <input
                    type='number'
                    name='monto'
                    class='form-control mt-3'
                    placeholder='Monto'
                    autoComplete='off'
                    v-model="editFactura.monto"

                />
                

                <button 
                class='btn btn-primary form-control  mt-2'
                type='submit'  
                
                >
                Actualizar

                </button>

            </div>
        </form>
        <button class="btn btn-success mt-5" @click="salirEdit" >Salir</button>
</template>

<script>
import { mapActions, mapGetters, mapMutations, mapState } from 'vuex'
export default {
    data(){
        return{
            editFactura:{}
        }
    },
    mounted(){
        this.editFactura = this.factura
    },
    computed:{
        ...mapState('facturar',['factura']),
        ...mapGetters('socio',['getSociosAll'])
    },
    methods:{
     ...mapActions('facturar',['updateFactura']),
     ...mapMutations('facturar',['salirEdit']),

     async updateFac() {
            
            await this.updateFactura(this.editFactura)
       }
    }
}
</script>

